.text-danger {
	color: #900;
}
body {
	padding-top: 145px;
}
@media (min-width: 768px) {
	body {
		padding-top: 100px;
	}
}
a {
	text-decoration: none !important;
}
@media (min-width: 768px) {
	section {
		padding: 3rem 0;
	}
}
footer {
	font-size: 12px;
	padding: 0 0 25px;
}
h3 small {
	font-size: .45em;
}
ul {
	padding: 0 0 0 15px;
}
nav {
	width: 100%;
	background: #36afc1;
	z-index: 99999;
	position: fixed;
	top: 0;
	padding: 15px 0;
}
.logo-link,.navbar-brand {
	width: 100%;
	display: block;
	text-align: center;
}
@media (min-width: 768px) {
	.logo-link,.navbar-brand {
		text-align: left;
		width: auto;
	}
}
.nav-inline {
	text-align: center;
	padding: 0 10px;
	margin-top: 20px;
}
@media (min-width: 768px) {
	.nav-inline {
		text-align: right;
	}
}
.nav-inline li {
	display: inline;
	padding: 0 10px;
	list-style: none;
	vertical-align: middle;
	font-size: 16px;
}
.nav-inline li a {
	color: #ffffff;
	font-weight: bold;
}
.page-header {
	background: #333;
	padding: 25px 0;
}
.schedule-section {
	margin-top: 45px;
}
section.schedule-section .row {
	display: flex;
	flex-direction: column;
}
.day {
	padding: 0 15px;
}
.schedule-warp {
	width: 100%;
	height: 100%;
	padding-right:20px;
	padding-left:20px;
	padding: 20px 10px 0;
	margin-bottom: 5%;
	border: solid 1px #ddd;
	background-color: #f5f5f5;
	-webkit-transition: all 0.7s cubic-bezier(0.11, 0.6, 0.24, 0.89);
	transition: all 0.7s cubic-bezier(0.11, 0.6, 0.24, 0.89);
}
.schedule {
	background-color:#fff;
}
.schedule-warp:hover {
	box-shadow: 0 6px 20px rgba(0,0,0,.07);
	border: solid 1px rgba(60,187,177,.97);
}
.schedule {
	padding:100px 0px;
	background-color: #ecedf0;
}
.schedule-tag {
	color:#fff;
	background-color: #36afc1;
	border: 1px solid #197886;
	padding:3px 8px;
	margin-right: 15px;
	width: 140px;
	min-width: 140px;
	text-align: center;
}
.schedule-header span {
	display: inline-block;
	margin-right:25px;
}
.schedule-header {
	margin-bottom:80px;
}
.schedule-card {
	position: relative;
	margin-bottom: 0.7em;
	box-shadow: 0 0px 5px rgba(0,0,0,.07);
	background: #ffffff;
	padding-bottom: 5px;
}
.insidebox {
	width: 100%;
	height: auto;
	display: flex;
	-webkit-align-items: center;
	align-items: center;
	padding: 10px 20px;
	color: #333;
	transition: all 0.2s ease-in;
}
h2.day-title {
	margin-top: 0;
	margin-bottom:12px;
	font-weight: bold;
	font-size: 18px;
	color: #076773;
}
h2.day-title small {
	letter-spacing: .95;
}
.schedule-more-info {
	display: block;
	width: 100%;
	padding: 0 15px;
}
.schedule-more-info .collapse .well {
	position: relative;
	text-align: left;
	margin-bottom: 8px;
	padding-bottom: 30px;
}
.schedule-more-info a {
	display: block;
	margin-bottom: 5px;
	color: #67afda;
}
.schedule-more-info ul > li > a {
	color: #0eb9b2;
}
.close-more-info {
	position: absolute;
	right: 8px;
	bottom: 2px;
}
.close-more-info a {
	color: #ee4266;
}
.timeline {
	list-style: none;
	padding: 20px 0 0;
	position: relative;
}
.timeline:before {
	top: 0;
	bottom: 0;
	position: absolute;
	content: " ";
	width: 3px;
	background-color: #eeeeee;
	left: 50%;
	margin-left: -1.5px;
}
.timeline > li {
	margin-bottom: 20px;
	position: relative;
	width: 50%;
	float: left;
	clear: left;
}
.timeline > li:before,.timeline > li:after {
	content: " ";
	display: table;
}
.timeline > li:after {
	clear: both;
}
.timeline > li:before,.timeline > li:after {
	content: " ";
	display: table;
}
.timeline > li:after {
	clear: both;
}
.timeline > li > .timeline-panel {
	width: 95%;
	float: left;
	border: 1px solid #d4d4d4;
	/*border-radius: 2px;
	*/
	/*padding: 20px;
	*/
	position: relative;
	-webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
	box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
}
.timeline > li > .timeline-panel:before {
	position: absolute;
	top: 26px;
	right: -15px;
	display: inline-block;
	border-top: 15px solid transparent;
	border-left: 15px solid #ccc;
	border-right: 0 solid #ccc;
	border-bottom: 15px solid transparent;
	content: " ";
}
.timeline > li > .timeline-panel:after {
	position: absolute;
	top: 27px;
	right: -14px;
	display: inline-block;
	border-top: 14px solid transparent;
	border-left: 14px solid #fff;
	border-right: 0 solid #fff;
	border-bottom: 14px solid transparent;
	content: " ";
}
.timeline > li > .timeline-badge {
	color: #fff;
	width: 24px;
	height: 24px;
	line-height: 50px;
	font-size: 1.4em;
	text-align: center;
	position: absolute;
	top: 16px;
	right: -12px;
	z-index: 100;
}
.timeline > li.timeline-inverted > .timeline-panel {
	float: right;
}
.timeline > li.timeline-inverted > .timeline-panel:before {
	border-left-width: 0;
	border-right-width: 15px;
	left: -15px;
	right: auto;
}
.timeline > li.timeline-inverted > .timeline-panel:after {
	border-left-width: 0;
	border-right-width: 14px;
	left: -14px;
	right: auto;
}
.timeline-badge > a {
	color: #C5C7C5 !important;
}
.timeline-badge a:hover {
	color: #000 !important;
}
.timeline-title {
	margin-top: 0;
	color: inherit;
}
.timeline-img-cont {
	padding: 25px 25px 0;
}
.timeline-img-cont img {
	margin-top: 15px;
}
.timeline-body {
	padding: 20px;
}
.timeline-body h3 {
	margin-top: 0;
	margin-bottom: 10px;
}
.timeline-body > p,.timeline-body > ul {
	margin-bottom: 0;
}
.timeline-body > p + p {
	margin-top: 5px;
}
.timeline-footer {
	padding: 20px;
	background-color: #f4f4f4;
}
.timeline-footer svg,.timeline-footer i {
	font-size: 12px;
}
.timeline-footer > a {
	cursor: pointer;
	text-decoration: none;
}
.tooltip {
	position: absolute;
	z-index: 1020;
	display: block;
	visibility: visible;
	padding: 5px;
	font-size: 11px;
	opacity: 0;
	filter: alpha(opacity=0);
}
.tooltip.in {
	/*opacity:0;
	filter:alpha(opacity=80);
	*/
}
.tooltip.top {
	margin-top: -2px;
}
.tooltip.right {
	margin-left: 2px;
}
.tooltip.bottom {
	margin-top: 2px;
}
.tooltip.left {
	margin-left: -2px;
}
.tooltip.top .tooltip-arrow {
	bottom: 0;
	left: 0;
	margin-left: 0;
	border-left: 0 solid transparent;
	border-right: 5px solid transparent;
	border-top: 0 solid #000;
}
.tooltip.left .tooltip-arrow {
	bottom: 0;
	left: 0;
	margin-left: 0;
	border-left: 0 solid transparent;
	border-right: 5px solid transparent;
	border-top: 0 solid #000;
}
.tooltip.bottom .tooltip-arrow {
	bottom: 0;
	left: 0;
	margin-left: 0;
	border-left: 0 solid transparent;
	border-right: 5px solid transparent;
	border-top: 0 solid #000;
}
.tooltip.right .tooltip-arrow {
	bottom: 0;
	left: 0;
	margin-left: 0;
	border-left: 0 solid transparent;
	border-right: 5px solid transparent;
	border-top: 0 solid #000;
}
.tooltip-inner {
	width: 200px;
	padding: 3px 8px;
	color: #fff;
	text-align: center;
	text-decoration: none;
	background-color: #313131;
	-webkit-border-radius: 0px;
	-moz-border-radius: 0px;
	border-radius: 0px;
}
.tooltip-arrow {
	position: absolute;
	width: 0;
	height: 0;
}
.timeline > li.timeline-inverted {
	float: right;
	clear: right;
	margin-top: 30px;
	margin-bottom: 30px;
}
.timeline > li:nth-child(2) {
	margin-top: 60px;
}
.timeline > li.timeline-inverted > .timeline-badge {
	left: -12px;
}
@media (max-width: 767px) {
	ul.timeline:before {
		left: 6px;
	}
	ul.timeline > li {
		margin-bottom: 20px;
		position: relative;
		width: 100%;
		float: left;
		clear: left;
	}
	ul.timeline > li > .timeline-panel {
		width: calc(100% - 25px);
		width: -moz-calc(100% - 25px);
		width: -webkit-calc(100% - 25px);
	}
	ul.timeline > li > .timeline-badge {
		left: 28px;
		margin-left: 0;
		top: 16px;
	}
	ul.timeline > li > .timeline-panel {
		float: right;
	}
	ul.timeline > li > .timeline-panel:before {
		border-left-width: 0;
		border-right-width: 15px;
		left: -15px;
		right: auto;
	}
	ul.timeline > li > .timeline-panel:after {
		border-left-width: 0;
		border-right-width: 14px;
		left: -14px;
		right: auto;
	}
	.timeline > li.timeline-inverted {
		float: left;
		clear: left;
		margin-top: 30px;
		margin-bottom: 30px;
	}
	.timeline > li.timeline-inverted > .timeline-badge {
		left: 28px;
	}
}
.timeline-footer a {
	padding: 0 4px;
}
.timeline-time {
	position: absolute;
	top: 7px;
	left: 8px;
	color: #fff;
	font-weight: bold;
	padding: 2px;
	background: #36afc1;
	border: 1px solid #197886;
	width: 74px;
	text-align: center;
}
@media (min-width: 768px) {
	.timeline-time.flip {
		left: 5px;
	}
	.timeline-time {
		right: 5px;
	}
}
